'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Progress } from '@/components/ui/progress'
import { Separator } from '@/components/ui/separator'

interface CreditFormData {
  aadhaarNumber: string
  monthlyIncome: string
  employmentType: string
  bankTransactions: string
  digitalFootprint: string
  socialConnections: string
  paymentHistory: string
  educationLevel: string
  residenceType: string
  familySize: string
}

interface CreditScoreData {
  creditScore: number
  category: string
  recommendation: string
  factors: {
    income: number
    employment: number
    digitalFootprint: number
    bankTransactions: number
    socialConnections: number
    paymentHistory: number
  }
  eligibleProducts: string[]
  generatedAt: string
}

export default function CreditablePage() {
  const [formData, setFormData] = useState<CreditFormData>({
    aadhaarNumber: '',
    monthlyIncome: '',
    employmentType: '',
    bankTransactions: '',
    digitalFootprint: '',
    socialConnections: '',
    paymentHistory: '',
    educationLevel: '',
    residenceType: '',
    familySize: ''
  })
  const [isLoading, setIsLoading] = useState(false)
  const [creditScore, setCreditScore] = useState<CreditScoreData | null>(null)
  const [error, setError] = useState('')

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target
    setFormData(prev => ({
      ...prev,
      [name]: value
    }))
  }

  const handleSelectChange = (name: string, value: string) => {
    setFormData(prev => ({
      ...prev,
      [name]: value
    }))
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsLoading(true)
    setError('')

    try {
      const response = await fetch('/api/credit-score', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          ...formData,
          monthlyIncome: parseFloat(formData.monthlyIncome),
          bankTransactions: parseFloat(formData.bankTransactions) || 0,
          digitalFootprint: parseFloat(formData.digitalFootprint) || 0,
          socialConnections: parseFloat(formData.socialConnections) || 0,
          paymentHistory: parseFloat(formData.paymentHistory) || 0
        }),
      })

      const result = await response.json()

      if (result.success) {
        setCreditScore(result.data)
      } else {
        setError(result.error || 'Failed to calculate credit score')
      }
    } catch (err) {
      setError('Network error. Please try again.')
    } finally {
      setIsLoading(false)
    }
  }

  const getScoreColor = (score: number) => {
    if (score >= 750) return 'text-green-600'
    if (score >= 650) return 'text-blue-600'
    if (score >= 550) return 'text-yellow-600'
    return 'text-red-600'
  }

  const getScoreProgress = (score: number) => {
    return (score / 850) * 100
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="max-w-4xl mx-auto">
        {/* Header */}
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold text-gray-900 mb-4">Creditable</h1>
          <p className="text-xl text-gray-600 mb-2">Alternative Data-Based Credit Scoring</p>
          <p className="text-gray-500">Fair credit assessment for everyone, including the unbanked</p>
        </div>

        {/* Information Cards */}
        <div className="grid md:grid-cols-3 gap-6 mb-8">
          <Card>
            <CardHeader className="text-center">
              <CardTitle className="text-lg">Traditional Credit</CardTitle>
            </CardHeader>
            <CardContent className="text-center">
              <div className="text-2xl font-bold text-gray-400 mb-2">Limited</div>
              <p className="text-sm text-gray-600">Based only on credit history and collateral</p>
            </CardContent>
          </Card>
          <Card className="border-black">
            <CardHeader className="text-center">
              <CardTitle className="text-lg">Creditable Score</CardTitle>
            </CardHeader>
            <CardContent className="text-center">
              <div className="text-2xl font-bold text-black mb-2">Inclusive</div>
              <p className="text-sm text-gray-600">Uses alternative data for fair assessment</p>
            </CardContent>
          </Card>
          <Card>
            <CardHeader className="text-center">
              <CardTitle className="text-lg">Impact</CardTitle>
            </CardHeader>
            <CardContent className="text-center">
              <div className="text-2xl font-bold text-green-600 mb-2">10M+</div>
              <p className="text-sm text-gray-600">Unbanked individuals can access credit</p>
            </CardContent>
          </Card>
        </div>

        {!creditScore ? (
          /* Credit Assessment Form */
          <Card>
            <CardHeader>
              <CardTitle>Credit Score Assessment</CardTitle>
              <CardDescription>
                Provide information to calculate your alternative credit score
              </CardDescription>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleSubmit} className="space-y-6">
                {/* Basic Information */}
                <div>
                  <h3 className="text-lg font-semibold mb-4">Basic Information</h3>
                  <div className="grid md:grid-cols-2 gap-4">
                    <div>
                      <Label htmlFor="aadhaarNumber">Aadhaar Number</Label>
                      <Input
                        id="aadhaarNumber"
                        name="aadhaarNumber"
                        value={formData.aadhaarNumber}
                        onChange={handleInputChange}
                        placeholder="Enter 12-digit Aadhaar number"
                        maxLength={12}
                        required
                      />
                    </div>
                    <div>
                      <Label htmlFor="monthlyIncome">Monthly Income (â‚¹)</Label>
                      <Input
                        id="monthlyIncome"
                        name="monthlyIncome"
                        type="number"
                        value={formData.monthlyIncome}
                        onChange={handleInputChange}
                        placeholder="Enter monthly income"
                        required
                      />
                    </div>
                    <div>
                      <Label htmlFor="employmentType">Employment Type</Label>
                      <Select onValueChange={(value) => handleSelectChange('employmentType', value)}>
                        <SelectTrigger>
                          <SelectValue placeholder="Select employment type" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="government">Government Employee</SelectItem>
                          <SelectItem value="private">Private Employee</SelectItem>
                          <SelectItem value="self-employed">Self Employed</SelectItem>
                          <SelectItem value="freelancer">Freelancer</SelectItem>
                          <SelectItem value="student">Student</SelectItem>
                          <SelectItem value="unemployed">Unemployed</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>
                    <div>
                      <Label htmlFor="educationLevel">Education Level</Label>
                      <Select onValueChange={(value) => handleSelectChange('educationLevel', value)}>
                        <SelectTrigger>
                          <SelectValue placeholder="Select education level" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="primary">Primary School</SelectItem>
                          <SelectItem value="secondary">Secondary School</SelectItem>
                          <SelectItem value="higher-secondary">Higher Secondary</SelectItem>
                          <SelectItem value="graduate">Graduate</SelectItem>
                          <SelectItem value="post-graduate">Post Graduate</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>
                  </div>
                </div>

                <Separator />

                {/* Alternative Data Factors */}
                <div>
                  <h3 className="text-lg font-semibold mb-4">Alternative Data Factors</h3>
                  <div className="grid md:grid-cols-2 gap-4">
                    <div>
                      <Label htmlFor="bankTransactions">Monthly Bank Transactions</Label>
                      <Input
                        id="bankTransactions"
                        name="bankTransactions"
                        type="number"
                        value={formData.bankTransactions}
                        onChange={handleInputChange}
                        placeholder="Average number of transactions per month"
                      />
                      <p className="text-xs text-gray-500 mt-1">Include UPI, NEFT, IMPS transactions</p>
                    </div>
                    <div>
                      <Label htmlFor="digitalFootprint">Digital Footprint Score (1-10)</Label>
                      <Input
                        id="digitalFootprint"
                        name="digitalFootprint"
                        type="number"
                        min="1"
                        max="10"
                        value={formData.digitalFootprint}
                        onChange={handleInputChange}
                        placeholder="Rate your digital activity"
                      />
                      <p className="text-xs text-gray-500 mt-1">Social media, online shopping, digital services usage</p>
                    </div>
                    <div>
                      <Label htmlFor="socialConnections">Professional Network Size</Label>
                      <Input
                        id="socialConnections"
                        name="socialConnections"
                        type="number"
                        value={formData.socialConnections}
                        onChange={handleInputChange}
                        placeholder="Number of professional contacts"
                      />
                      <p className="text-xs text-gray-500 mt-1">LinkedIn connections, business contacts</p>
                    </div>
                    <div>
                      <Label htmlFor="paymentHistory">Payment Reliability (1-5)</Label>
                      <Select onValueChange={(value) => handleSelectChange('paymentHistory', value)}>
                        <SelectTrigger>
                          <SelectValue placeholder="Rate payment reliability" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="5">Always on time</SelectItem>
                          <SelectItem value="4">Usually on time</SelectItem>
                          <SelectItem value="3">Sometimes late</SelectItem>
                          <SelectItem value="2">Often late</SelectItem>
                          <SelectItem value="1">Frequently missed</SelectItem>
                        </SelectContent>
                      </Select>
                      <p className="text-xs text-gray-500 mt-1">Utility bills, rent, EMI payments</p>
                    </div>
                  </div>
                </div>

                <Separator />

                {/* Additional Information */}
                <div>
                  <h3 className="text-lg font-semibold mb-4">Additional Information</h3>
                  <div className="grid md:grid-cols-2 gap-4">
                    <div>
                      <Label htmlFor="residenceType">Residence Type</Label>
                      <Select onValueChange={(value) => handleSelectChange('residenceType', value)}>
                        <SelectTrigger>
                          <SelectValue placeholder="Select residence type" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="owned">Owned</SelectItem>
                          <SelectItem value="rented">Rented</SelectItem>
                          <SelectItem value="family">Family Owned</SelectItem>
                          <SelectItem value="company">Company Provided</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>
                    <div>
                      <Label htmlFor="familySize">Family Size</Label>
                      <Input
                        id="familySize"
                        name="familySize"
                        type="number"
                        value={formData.familySize}
                        onChange={handleInputChange}
                        placeholder="Number of family members"
                      />
                    </div>
                  </div>
                </div>

                {error && (
                  <Alert className="border-red-200 bg-red-50">
                    <AlertDescription className="text-red-800">
                      {error}
                    </AlertDescription>
                  </Alert>
                )}

                <Button 
                  type="submit" 
                  className="w-full bg-black hover:bg-gray-800"
                  disabled={isLoading}
                >
                  {isLoading ? 'Calculating Credit Score...' : 'Calculate My Credit Score'}
                </Button>
              </form>
            </CardContent>
          </Card>
        ) : (
          /* Credit Score Results */
          <div className="space-y-6">
            {/* Main Score Card */}
            <Card className="border-2 border-black">
              <CardHeader className="text-center">
                <CardTitle className="text-2xl">Your Creditable Score</CardTitle>
                <CardDescription>Generated on {new Date(creditScore.generatedAt).toLocaleDateString()}</CardDescription>
              </CardHeader>
              <CardContent className="text-center space-y-4">
                <div className={`text-6xl font-bold ${getScoreColor(creditScore.creditScore)}`}>
                  {creditScore.creditScore}
                </div>
                <div className="text-xl font-semibold text-gray-700">
                  {creditScore.category}
                </div>
                <Progress 
                  value={getScoreProgress(creditScore.creditScore)} 
                  className="w-full max-w-md mx-auto h-3"
                />
                <div className="text-sm text-gray-500">
                  Score Range: 300 - 850
                </div>
                <div className="bg-gray-50 p-4 rounded-lg">
                  <h4 className="font-semibold mb-2">Recommendation</h4>
                  <p className="text-gray-700">{creditScore.recommendation}</p>
                </div>
              </CardContent>
            </Card>

            {/* Score Factors Breakdown */}
            <Card>
              <CardHeader>
                <CardTitle>Score Factors Breakdown</CardTitle>
                <CardDescription>How different factors contributed to your score</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  {Object.entries(creditScore.factors).map(([factor, score]) => (
                    <div key={factor} className="flex items-center justify-between">
                      <div className="flex-1">
                        <div className="flex justify-between items-center mb-1">
                          <span className="text-sm font-medium capitalize">
                            {factor.replace(/([A-Z])/g, ' $1').trim()}
                          </span>
                          <span className="text-sm text-gray-600">{score} points</span>
                        </div>
                        <Progress value={(score / 200) * 100} className="h-2" />
                      </div>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>

            {/* Eligible Products */}
            <Card>
              <CardHeader>
                <CardTitle>Eligible Financial Products</CardTitle>
                <CardDescription>Based on your credit score, you may qualify for:</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="grid md:grid-cols-2 gap-4">
                  {creditScore.eligibleProducts.map((product, index) => (
                    <div key={index} className="p-4 border rounded-lg hover:shadow-md transition-shadow">
                      <h4 className="font-semibold text-gray-900">{product}</h4>
                      <p className="text-sm text-gray-600 mt-1">
                        {product === 'Personal Loan' && 'Unsecured loans for personal needs'}
                        {product === 'Credit Card' && 'Credit cards with various benefits'}
                        {product === 'Business Loan' && 'Loans for business expansion'}
                        {product === 'Secured Credit Card' && 'Credit card with security deposit'}
                        {product === 'Fixed Deposit Loan' && 'Loan against fixed deposits'}
                      </p>
                      <Button size="sm" variant="outline" className="mt-2">
                        Apply Now
                      </Button>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>

            {/* Improvement Tips */}
            <Card>
              <CardHeader>
                <CardTitle>Tips to Improve Your Score</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="grid md:grid-cols-2 gap-4">
                  <div className="space-y-3">
                    <div className="flex items-start space-x-3">
                      <div className="w-2 h-2 bg-black rounded-full mt-2"></div>
                      <div>
                        <h4 className="font-medium">Increase Digital Activity</h4>
                        <p className="text-sm text-gray-600">Use digital payment methods and online services regularly</p>
                      </div>
                    </div>
                    <div className="flex items-start space-x-3">
                      <div className="w-2 h-2 bg-black rounded-full mt-2"></div>
                      <div>
                        <h4 className="font-medium">Maintain Payment History</h4>
                        <p className="text-sm text-gray-600">Pay all bills and EMIs on time consistently</p>
                      </div>
                    </div>
                    <div className="flex items-start space-x-3">
                      <div className="w-2 h-2 bg-black rounded-full mt-2"></div>
                      <div>
                        <h4 className="font-medium">Build Professional Network</h4>
                        <p className="text-sm text-gray-600">Expand your professional connections and references</p>
                      </div>
                    </div>
                  </div>
                  <div className="space-y-3">
                    <div className="flex items-start space-x-3">
                      <div className="w-2 h-2 bg-black rounded-full mt-2"></div>
                      <div>
                        <h4 className="font-medium">Increase Bank Activity</h4>
                        <p className="text-sm text-gray-600">Use your bank account for more transactions</p>
                      </div>
                    </div>
                    <div className="flex items-start space-x-3">
                      <div className="w-2 h-2 bg-black rounded-full mt-2"></div>
                      <div>
                        <h4 className="font-medium">Stable Employment</h4>
                        <p className="text-sm text-gray-600">Maintain steady employment or business income</p>
                      </div>
                    </div>
                    <div className="flex items-start space-x-3">
                      <div className="w-2 h-2 bg-black rounded-full mt-2"></div>
                      <div>
                        <h4 className="font-medium">Regular Monitoring</h4>
                        <p className="text-sm text-gray-600">Check your score regularly and track improvements</p>
                      </div>
                    </div>
                  </div>
                </div>
              </CardContent>
            </Card>

            {/* Action Buttons */}
            <div className="flex flex-col sm:flex-row gap-4 justify-center">
              <Button 
                onClick={() => {
                  setCreditScore(null)
                  setFormData({
                    aadhaarNumber: '',
                    monthlyIncome: '',
                    employmentType: '',
                    bankTransactions: '',
                    digitalFootprint: '',
                    socialConnections: '',
                    paymentHistory: '',
                    educationLevel: '',
                    residenceType: '',
                    familySize: ''
                  })
                }}
                variant="outline"
              >
                Calculate Again
              </Button>
              <Button className="bg-black hover:bg-gray-800">
                Download Score Report
              </Button>
              <Button variant="outline">
                Share with Lender
              </Button>
            </div>
          </div>
        )}
      </div>
    </div>
  )
}
